export default {
	components: {
		
	},
	data() {
		return {
			h: 0,
			w: 0,
			
			
		};
	},

	onLoad() {
		
		uni.getSystemInfo({
			success: res => {
				// console.log(res.windowHeight);
				// console.log(res.windowWidth);
				this.h = res.windowHeight;
				this.w = res.windowWidth;
				
			}
		});
	},

	methods: {
		phonelogin(){
			uni.navigateTo({
				url: './phonelogin',

			});
		},
		wxlogin(){
			uni.login({
				provider:'weixin',
				success: (res) => {
					let js_code = res.code;
					console.log(js_code);
					uni.getUserInfo({
						provider:'weixin',
						success: (userres) => {
							uni.setStorage({
								key:res.code,
							})
						},
					})
					
				},
			})
			uni.navigateTo({
				url: './logined',
			});
		},
		
	}
};
